<template>
	<div id="index">
		<template v-if="showMoneyImg">
			<money-img-component v-on:clear="clear" v-bind:desk="desk" v-bind:money="money"></money-img-component>
		</template>
		<header-component v-on:turn="turn" v-bind:desk="desk" v-bind:index="index"></header-component>
		<div id="content" v-on:touchstart="touchstart" v-on:touchmove="touchmove" v-on:touchend="touchend" v-on:touchcancel="touchcancel">
			<!--<keep-alive>
			  <component v-bind:is="current" v-on:sendAdd="sendAdd" v-on:sendMinus="sendMinus"></component>
			</keep-alive>-->

			<template v-for="componentList in componentState">
				<template v-if="componentList.loading">
					<loading-component></loading-component>
				</template>
				<template v-if="componentList.show">
					<keep-alive>
						<food-component v-bind:foodList="componentList.foodList" v-on:add="add" v-on:minus="minus" v-on:load="load"></food-component>
					</keep-alive>
				</template>
			</template>

			<div class="clearfix"></div>
		</div>
		<pay-component v-on:showImg="showImg" v-bind:orderInfo="orderInfo" v-bind:money="money" v-bind:desk="desk"></pay-component>
  </div>
</template>

<script type="text/javascript">
	import Vue from 'vue';
	import headerComponent from './header.vue'  //header
  import payComponent from './pay.vue'  //footer
  import loadingComponent from './loading.vue'  //loading
  import foodComponent from './food.vue'

	const moneyImgComponent = Vue.component(  //
		'moneyImgComponent', 
		function (resolve) {
	    require(['./moneyImg.vue'],resolve)
    }
  );

	export default{
		components:{
			headerComponent,
			foodComponent,
			payComponent,
			moneyImgComponent,
			loadingComponent
		},
	  data(){
		  return{
		  	orderInfo: [],  //
		  	desk: '1',  //
		  	money: 0,  //
		  	showMoneyImg: false,  //
		  	index: 0,  //
	      startX: 0,  //XY
	      startY: 0,
	      endX: 0,
	      endY: 0,
	      startDate: 0,  //
	      endDate: 0,  //
	      slideState: '',  //
	      globalID: null,
	      componentState: [  //
	      	{
	      		show: false,  //
	      		loading: true,  //loading
	      		foodList: [
		          {img:require('../image/bean-curd.jpg'),name:'',price:8,num:0},
		          {img:require('../image/braised-eggplant.jpg'),name:'',price:8,num:0},
		          {img:require('../image/green-pepper-bacon.jpg'),name:'',price:8,num:0},
		          {img:require('../image/green-pepper-shredded-meat.jpg'),name:'',price:8,num:0},
		          {img:require('../image/kung-pao-chicken.jpg'),name:'',price:8,num:0},
		          {img:require('../image/leek-egg.jpg'),name:'',price:8,num:0},
		          {img:require('../image/sour-hot-potato.png'),name:'',price:8,num:0},
		          {img:require('../image/stew-pork.jpg'),name:'',price:8,num:0},
		          {img:require('../image/tomato-agg.png'),name:'',price:8,num:0},
		        ],
	      	},
	      	{
	      		show: false,
	      		loading: true,
	      		foodList:[
		          {img:require('../image/Lanzhou-beef-noodles.jpeg'),name:'',price:8,num:0},
		          {img:require('../image/Daoxiao-noodles.jpg'),name:'',price:8,num:0},
		          {img:require('../image/fried-sauce-noodles.jpg'),name:'',price:8,num:0},
		          {img:require('../image/stewed-noodles.jpg'),name:'',price:8,num:0},
		          {img:require('../image/tomato-egg-noodles.jpg'),name:'',price:8,num:0},
		          {img:require('../image/Chongqing-small-noodles.jpg'),name:'',price:8,num:0},
		          {img:require('../image/plain-noodle-soup.jpg'),name:'',price:8,num:0},
		        ],
	      	},
	      	{
	      		show: false,
	      		loading: true,
	      		foodList:[
              {img:require('../image/lily-almond-gruel.jpg'),name:'',price:8,num:0},
              {img:require('../image/purple-sweet-potato-gruel.jpeg'),name:'',price:8,num:0},
              {img:require('../image/jujube-yam.jpg'),name:'',price:8,num:0},
              {img:require('../image/red-bean-millet-gruel.png'),name:'',price:8,num:0},
              {img:require('../image/tremella-lotus-porridge.jpeg'),name:'',price:8,num:0},
              {img:require('../image/sydney-rice-gruel.jpeg'),name:'',price:8,num:0},
            ]
	      	},
	      	{
	      		show: false,
	      		loading: true,
	      		foodList:[
              {img:require('../image/cola.jpg'),name:'',price:5,num:0},
              {img:require('../image/grapefruit-tea.jpg'),name:'',price:5,num:0},
              {img:require('../image/orange-juice.jpg'),name:'',price:5,num:0},
              {img:require('../image/syrup-plum.jpg'),name:'',price:5,num:0},
              {img:require('../image/sprite.jpg'),name:'',price:5,num:0},
            ],
	      	}
	      ],
		  }
	  },
	  methods:{
	  	add:function(name,num,price){
	  		this.money=0;
	  		if(this.orderInfo.length==0){
	  			this.orderInfo.push({food_name:name,food_num:num,food_price:price});
	  		}else{
	  			let j=0;
	  			let i;
	  			for(i=0; i<this.orderInfo.length; i++){
	  				if(this.orderInfo[i].food_name==name){
	  					this.orderInfo[i].food_num++;
	  				}else{
	  					j++;
	  				}
	  			}
	  			if(j==i){
	  				this.orderInfo.push({food_name:name,food_num:num,food_price:price});
	  			}
	  		}
	  		for(let i=0; i<this.orderInfo.length; i++){
	  			this.money+=this.orderInfo[i].food_num*this.orderInfo[i].food_price;
	  		}
	  	},
	  	minus:function(name,price){
	  		this.money=0;
	  		for(let i=0; i<this.orderInfo.length; i++){
	  			if(this.orderInfo[i].food_name==name){
	  				this.orderInfo[i].food_num--;
	  				if(this.orderInfo[i].food_num==0){
	  			    this.orderInfo.splice(i,1)
	  		    }
	  				break;
	  			}
	  		}
	  		for(let i=0; i<this.orderInfo.length; i++){
	  			this.money+=this.orderInfo[i].food_num*this.orderInfo[i].food_price
	  		}
	  	},
	  	touchstart:function(event){
	  		var touch=event.targetTouches[0];
	  		this.endX=0;
	  		this.endY=0;
	  		this.startX=touch.pageX;
	  		this.startY=touch.pageY;
	  		this.startDate=+new Date();
	  	},
	  	animation: function(event){
	  		var touch=event.targetTouches[0];
	  		this.endX=touch.pageX;
	  		this.endY=touch.pageY;
	  		if(Math.abs(this.endY-this.startY)>Math.abs(this.endX-this.startX)){  //
	  			if(this.slideState==""){  //
	  				this.slideState="noSlide";  //
	  			}
	  		}else{  //
	  			if(this.slideState==""){
	  				this.slideState="slide";
	  			}
	  		}
	  		if(this.slideState=="slide"){
	  			event.preventDefault();  //
	  			if(this.index==0&&(this.endX-this.startX)>0){  //
	  				this.startX=this.endX;
	  				return;
	  			}else if(this.index==3&&(this.endX-this.startX)<0){  //
	  				this.startX=this.endX;
	  				return;
	  			}else{
	  			  $("#content").css("transform",'translateX('+(this.endX-this.startX-this.index*640)+'px)');  //
	  			}
	  		}
	  	},
	  	touchmove:function(event){
	  		this.globalID=window.requestAnimationFrame( () => {
          this.animation(event);
        });
	  	},
	  	touchend:function(event){
	  		cancelAnimationFrame(this.globalID);
	  		this.endDate=+new Date();
	  		var timeDifference=this.endDate-this.startDate;  //
	  		var distanceX=this.endX-this.startX;  //
	  		if(this.slideState=="slide"){
	  			if(timeDifference<=250&&distanceX>60&&this.index>0){  //250ms 60
	  				this.index--;  //
	  			}else if(timeDifference<=250&&distanceX<-60&&this.index<3){
	  				this.index++;  //
	  			}else if(distanceX>=160&&this.index>0){  //160
	  				this.index--;  //
	  			}else if(distanceX<=-160&&this.index<3){
	  				this.index++;  //
	  			}
	  			$("#content").css({  //
	  				'transform': 'translateX('+(-this.index*640)+'px)',
	  				'transition': 'transform 0.25s linear' 
	  			});
	  			this.componentState[this.index].show=true;  //
	  		}
	  		this.slideState="";  //
	  	},
	  	touchcancel:function(){
				$("#content").css({  //
					'transform': 'translateX('+(-this.index*640)+'px)',
					'transition': 'transform 0.25s linear' 
				});
	  	},
	  	load:function(){  //loading
	  		this.componentState[this.index].loading=false;
	  	},
	  	turn:function(index){  //
  		  this.index=index;
  			$("#content").css({  //
  				'transform': 'translateX('+(-this.index*640)+'px)',
  				'transition': 'transform 0.25s linear' 
  			})
  			this.componentState[this.index].show=true;  //
	  	},
	  	showImg:function () {  //
				this.showMoneyImg = !this.showMoneyImg;
			},
			clear:function(){
				window.location.reload();
			}
	  },
	  created:function(){
	  	this.componentState[0].show=true;  //
	  },
	}
</script>

<style>

</style>


